import { useFormik } from "formik";
import { KeyboardEvent, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { addMeal } from "../redux/mealSlise";
import { calcWordEnding, calc–°alories } from "../utils/math";
import { formInitialValues } from "../utils/types";
import { mealsValidationSchema } from "../utils/validation";
import { FormContainer, Grid, SaveButton, Title } from "./Form.styles";
import Input from "./Input";

function Form() {
  const dispatch = useDispatch();

  const [calories, setCalories] = useState(0);

  const formik = useFormik({
    initialValues: {
      protein: 12.6,
      fat: 3.3,
      carb: 57.1,
      weight: 100,
    } as formInitialValues,
    validationSchema: mealsValidationSchema,
    onSubmit: (values, { resetForm }) => {
      dispatch(addMeal({ ...values, calories }));
      resetForm();
    },
  });

  const { protein, fat, carb, weight } = formik.values;

  useEffect(() => {
    setCalories(calc–°alories(formik.values));
  }, [formik.values]);

  function onKeyPress(evt: KeyboardEvent<HTMLInputElement>) {
    if (
      evt.key === "-" ||
      Number(evt.currentTarget.value) > 99.99 ||
      (evt.currentTarget.value === "0" && /^[0]/.test(evt.key))
    ) {
      evt.preventDefault();
    }
  }

  return (
    <FormContainer onSubmit={formik.handleSubmit}>
      <Title>–ë–ñ–£ –Ω–∞ 100 –≥—Ä–∞–º–º:</Title>
      <Grid>
        <Input
          formik={formik}
          value={protein}
          name="protein"
          placeholder="–ë–µ–ª–∫–∏"
          type={"number"}
          onKeyPress={onKeyPress}
          onFocus={() => formik.setFieldValue("protein", "")}
        />
        <Input
          formik={formik}
          value={fat}
          name="fat"
          placeholder="–ñ–∏—Ä—ã"
          type={"number"}
          onKeyPress={onKeyPress}
          onFocus={() => formik.setFieldValue("fat", "")}
        />
        <Input
          formik={formik}
          value={carb}
          name="carb"
          placeholder="–£–≥–ª–µ–≤–æ–¥—ã"
          type={"number"}
          onKeyPress={onKeyPress}
          onFocus={() => formik.setFieldValue("carb", "")}
        />
      </Grid>
      <Title>–í–µ—Å –ø—Ä–æ–¥—É–∫—Ç–∞:</Title>
      <Input
        formik={formik}
        value={weight}
        name="weight"
        placeholder="–ì—Ä–∞–º–º—ã"
        type={"number"}
        onKeyPress={onKeyPress}
        onFocus={() => formik.setFieldValue("weight", "")}
      />
      <SaveButton
        type="submit"
        aria-label="–î–æ–±–∞–≤–∏—Ç—å –∑–∞–ø–∏—Å—å"
        disabled={!formik.isValid}
      >
        {` –î–æ–±–∞–≤–∏—Ç—å ${
          calories === 0
            ? `–Ω–µ–º–Ω–æ–≥–æ –∫–∏–ª–æ–∫–∞–ª–æ—Ä–∏–π`
            : `${calories} ${calcWordEnding(calories)}`
        }`}
      </SaveButton>
    </FormContainer>
  );
}

export default Form;
